.label-mrgn {
  margin-top: 10px;
}

body {
  width: 750px;
  width: 1000px;
  position: absolute;
  margin: auto;
  left: 0;
  right: 0;
}
#root {
  width: 1000px;
  background-color: #f6f4f4;  
}

.react-grid-Canvas {
  overflow: hidden !important;
  height: auto !important;
}
.react-grid-Container {
  width: 935px !important;
}

.react-grid-HeaderRow {
  overflow: hidden !important;
  width: 100%;
}
.mrgn-btm-20px {
  margin-bottom: 20px;
}

.mrgn-left-30px {
  margin-left: 30px;
}
.App {
  text-align: center;
}

.App-logo {
  -webkit-animation: App-logo-spin infinite 20s linear;
          animation: App-logo-spin infinite 20s linear;
  height: 80px;
}

.App-header {
  background-color: #222;
  height: 150px;
  padding: 20px;
  color: white;
}



.App-title {
  font-size: 1.5em;
}

.App-intro {
  font-size: large;
}

.chefmenu{
  width: 95% !important;
  padding: 10px 20px;  
}

